.container {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: relative;
  background: #f7f7f7;
}

.title {
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 24px;
  font-family: Arial, sans-serif;
}

.box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  height: 80%;
  border-radius: 15px;
  border: 2px solid black;
  overflow: hidden;
  background: white;
}

.grid {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(90deg, #ccc 1px, transparent 1px),
    linear-gradient(0deg, #ccc 1px, transparent 1px);
  background-size: 50px 50px;
}

.timeline {
  position: absolute;
  width: 2px;
  height: 100%;
  background-color: blue;
  z-index: 2;
}

.blue-line {
  position: absolute;
  top: 50%;
  width: 2px;
  height: 100%;
  background-color: blue;
  transform: translateY(-50%);
}

.current-time-circle {
  position: absolute;
  top: 50%;
  left: -5px;
  width: 10px;
  height: 10px;
  background-color: blue;
  border-radius: 50%;
  transform: translateY(-50%);
}
